import React, { Component } from "react";
import Checkbox from "./Checkbox";
import {filterCatagories} from '../data/filtercatagorydata';

export default class ChoiceContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checkedItems: new Map()
    }
  }

  onCheckboxChange = event => {
    const item = event.target.name;
    const isChecked = event.target.checked;
    this.setState(prevState => ({
      checkedItems: prevState.checkedItems.set(item, isChecked)
    }));
  };

  render() {
    const checkboxes = filterCatagories[0].options.map(item => {
      <label key={item.key}>
          <Checkbox name={item.name} checked={this.state.checkedItems.get(item.name)} onChange={this.onCheckboxChange} />
          <span>{item.label}</span>
        </label>
    })
    return <div>{checkboxes}</div>;
  }
}
